@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;800&display=swap);
body {
  font-weight: 300;
  font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

/*  --- AUTH --- */

/* signup  */

.title-signup {
  margin: 60px 0 40px 0;
  text-align: center;
  font-weight: 600;
  font-size: 1.8em;
  text-transform: uppercase;
  color: #1a6a9f;
}

.signup-text {
  font-size: 1.3em;
  margin-bottom: 40px;
}

.btn-signup {
  border: none;
  border-radius: 40px;
  background-color: #1a6a9f;
  padding: 10px;
}

.signup-a {
  color: #1a6a9f;
  font-weight: 500;
}

/* login */

.title-login {
  margin: 80px 0 40px 0;
  text-align: center;
  font-weight: 600;
  font-size: 1.8em;
  text-transform: uppercase;
  color: #1a6a9f;
}

.login-text {
  text-align: start;
  font-size: 1.3em;

}

.btn-login {
  border: none;
  border-radius: 40px;
  background-color: #1a6a9f;
  padding: 10px;

}

.login-a {
  color: #1a6a9f;
  font-weight: 500;
}


.error-message {
  margin: 10px 0;
  background: red;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 1em;
  font-weight: 500;
}
/* -- MARKER -- */


.marker{
  width:50px;
  height: 50px;
}

.marker img {
  display: block;
  object-fit: contain;
  width: 100%;
}

.restaurants-list .card{   
    margin-bottom: 30px;
    min-height: 430px;
    box-shadow: grey;
}

.restaurants-list img {
    height: 180px;
    object-fit: cover;
}

h3 {
    font-size: 1.5em;
    font-weight: 500;
}

h5 {
    font-weight: 600;
    font-size: 1.4em;
}


.btn {
    font-weight: 500;
    font-size: .9em;
}

.btn-choose img {
    width: 25px;
    margin: 0 2px;
  }

  .btn-choose {
    font-weight: 500;
    font-size: 2em;
    border: none;
    border-radius: 40px;
    background-color: #1a6a9f;
    margin: 50px 0;
    padding: 10px;
  }

.add-rest {
    margin: 20px 0;
}

.btn-add {
    font-weight: 500;
    font-size: 1.4em;
    padding: 10px 35px;
    margin: 15px 0;
    border: none;
    border-radius: 40px;
    background-color: #1a6a9f;
}
  /* --- RESTAURANT CARD -- */

  .btn-detail {
      border: none;
      background-color: #1a6a9f;
      border-radius: 40px;
      color: white;
      padding: 10px 15px;
  }

  .btn:hover {
    color: white;
    background-color: #185c8a;
}


.toast {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 99999;
    width: 350px;
  }


  /* FILTROS */
  .filter-text {
      margin-top: 30px;
      font-weight: 500;
      font-size: 1.2em;
  }
.restaurant-form {
    margin-bottom: 20px;

}

.title {
    font-size: 1.8em;
    font-weight: 600;
    margin-bottom: 20px;
    text-align: center;
    text-transform: uppercase;
    color: #1a6a9f;

}


.btn-close {
    margin: 0 0 0 410px; 
    background-color: transparent;
    border: none;
}

.btn-close:hover {
    margin: 0 0 0 410px; 
    background-color: transparent;
    border: none;
}


.btn-close img {
    width: 20px;
}
 /* --- RESTAURANT DETAIL */
.restaurant-info {
    padding-top: 20px;
}

 .title-card {
    font-size: 2em;
    font-weight: 600;

 }

.text-card {
    font-size: 1.3em;
}

.icon-list {
    width: 25px;
    cursor: pointer;
}

.icons {
    background-color: transparent;
    border: none;
}

.icons:hover {
    background-color: transparent;
    border: none;
}

.img-detail {
    height: 350px;
    object-fit: cover;
}



 /* --- REVIEW CARD */

.comment-title {
    margin: 10px 0;
    text-align: center;
    font-weight: 500;
    padding: 15px;
}

h5 {
    text-align: right;
    font-weight: 500;
    margin: 10px 0;
}

.img-rating {
    width: 20px;
    margin: 0 3px 3px 5px;
}

.comment {
    margin-bottom: 10px;
}

.avatar {
    border-radius: 50%;
    width: 60px;
    margin: 10px;
}

h6 {
    font-size: 1.4em;
}

.rating-info {
    font-size: 1.15em;
    font-weight: 500;
    text-align: end;
    margin-bottom: 20px;
}

.btn-back {
    margin-bottom: 20px;
    border: none;
    border-radius: 40px;
    background-color: #1a6a9f;
    padding: 10px 20px;
}

.delete-comment {
    width: 21px;
    margin: 0 0 3px 5px;
}

/* --- REVIEWS FORM */

.comment-form {
    margin: 20px 0;
}

.btn-comment {
    border: none;
    border-radius: 40px;
    background-color: #1a6a9f;
    padding: 10px 20px;
}
.navbar-a a {
  color:white;
  text-decoration: none;
  font-weight: 500;
}

.navbar-a a:hover {
  font-size: 1.15em;
  transition: .3s;
  font-weight: 500;
  color:white;
  text-decoration: none;
}

.navbar {
  margin-bottom: 40px;
  background-color: #185c8a;
  padding: 15px;

}
.greeting {
  color: white;
}
  .navbar-text{
    color: white;
  }
  .logo-navbar {
    margin-left: 20px;
  }
  .logo-navbar:hover {
    transition: 1s;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -ms-transition: 1s;
    -o-transition: 1s;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
.home {
  margin: 0 50px;
  padding: 100px;
}

.text-home {
  margin-top: 50px;
  font-size: 5em;
  font-weight: 800;
  color: #1a6b9f;
  text-transform: uppercase;
  }
  
p {
  font-weight: 400;
  font-size: 1.4em;
  text-align: center;
  text-align: left;
}

.text-bold {
  font-weight: 500;
}

.btn-choose-home {
  font-weight: 500;
  font-size: 1.4em;
  padding: 10px 35px;
  margin: 15px 0;
  border: none;
  border-radius: 40px;
  background-color: #1a6a9f;
  text-transform: uppercase;
  letter-spacing: 2px;

}

.bg {
  margin-top: 50px;
  width: 600px;
  object-fit: cover;
  opacity: .6;
}


.profile {

}
.avatar-profile {
    border-radius: 100%;
    display: block;
    margin: auto;
    width: 120px;
    margin-top: 30px;
}

.title-profile {
    margin: 20px 0;
    font-size: 1.5em;
    font-weight: 500;
    text-align: center;
}

.user {
    margin-bottom: 20px;
    min-height: 250px;
    border-radius: 5px;
    background-color: #1a6a9f;

}

.favs {
    padding: 15px;
    min-height: 250px;

}

.wish {
    padding: 15px;
    min-height: 250px;

}

.rest {
    margin-left: 5px;
    text-align: center;
}

.rest a  {
    color: black;
    font-size: 1em;
    font-weight: 400;
}

.rest a:hover {
    text-decoration: none;
    font-size: 1.1em;
    transition: .3s;
    color: #1a6a9f;
}

.icon-list-profile {
    width: 40px;
    margin: 0 10px;   
}
